ul {
  list-style-type: disc;
  padding-left: 15px; }

li {
  line-height: 24px; }
  li ul {
    margin-left: 24px; }

p, ul, ol {
  font-size: 16px;
  line-height: 24px; }

pre {
  padding: 0px 24px;
  max-width: 800px;
  white-space: pre-wrap; }

code {
  font-family: Consolas, Monaco, Andale Mono, monospace;
  line-height: 1.5;
  font-size: 13px;
  margin-bottom: 24px; }
  *:not(pre) > code {
    background-color: #f8f8f8;
    color: #e96900;
    padding: 1px 5px;
    margin: 0; }

hr {
  width: 100%;
  text-align: left;
  margin: 20px auto 20px 0;
  color: #35495e; }

canvas {
  display: block; }
  canvas:focus {
    outline: none; }

table {
  border-collapse: collapse;
  margin: 1rem 0;
  display: block;
  overflow-x: auto; }
  table thead {
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit; }
  table tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit; }
    table tr:nth-child(2n) {
      background-color: #f6f8fa; }
  table th {
    font-weight: bold;
    text-align: -internal-center; }
  table td, table th {
    border: 1px solid #dfe2e5;
    padding: .6em 1em; }
  table tbody {
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit; }
